import React, { useEffect } from 'react'
import { graphql } from 'gatsby'

import * as Elements from '../components/elements'
import { Layout } from '../layout'
import { Head } from '../components/head'
import { PostTitle } from '../components/post-title'
import { PostDate } from '../components/post-date'
import { PostContainer } from '../components/post-container'
import { Bio } from '../components/bio'
import { PostNavigator } from '../components/post-navigator'
import { Utterences } from '../components/utterances'
import * as ScrollManager from '../utils/scroll'

import CC from '../../assets/cc.svg'
import BY from '../../assets/cc-by.svg'
import ND from '../../assets/cc-nd.svg'
import NC from '../../assets/cc-nc.svg'

import '../styles/code.scss'

export default ({ data, pageContext, location }) => {
  useEffect(() => {
    ScrollManager.init()
    return () => ScrollManager.destroy()
  }, [])

  const post = data.markdownRemark
  const metaData = data.site.siteMetadata
  const { title, comment } = metaData
  const { utterances } = comment

  return (
    <Layout location={location} title={title} maxWidth={32} className="post" home={true}>
      <Head title={post.frontmatter.title} description={post.excerpt} keywords={post.frontmatter.keywords} />
      <PostTitle title={post.frontmatter.title} />
      <div style={{display: 'flex', justifyContent: "space-between"}} >
        <div style={{display: 'flex', gap: "3px"}}>
          <CC />
          <BY />
          <ND />
          <NC />
        </div>
        <PostDate date={post.frontmatter.date} />
      </div>
      <a href="https://goidle.github.io/react/noti" target="_blank" >2차 창작물에 대해서(유튜브, 블로그)</a>
      <PostContainer html={post.html} />
      <Elements.Hr />
      <Bio />
      <PostNavigator pageContext={pageContext} />
      <Utterences repo={utterances} />
    </Layout>
  )
}

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
        comment {
          utterances
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 280)
      html
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD")
        keywords
      }
    }
  }
`
